    .backgroundimg {
    height: 400px;
    width: 100%;
    }
    
    .txtOverlay{
        font-size: 2em;
    }
    
    .cardimg{
    height: 250px;
    width: 80%;
    }
    
    .heartOverlay {
        font-size: 1.2em;
    }
    
    }
    
